@import url("https://fonts.googleapis.com/css?family=Montserrat");
a, button {
  -webkit-tap-highlight-color: transparent; }

h1, h2, h3, h4, h5 {
  color: #686868;
  margin: 20px; }

h2 {
  font-size: 3.5em;
  font-weight: 400; }

h3 {
  font-size: 2.5em;
  font-weight: 400; }

h4 {
  font-size: 1.5em;
  font-weight: 400; }

p {
  font-size: 1.2em;
  margin: 20px;
  text-align: center; }

.Section {
  color: #686868;
  font-size: 1.2em;
  margin: 50px 0 50px 0; }

@-webkit-keyframes column-up {
  0% {
    -webkit-transform: rotate(1deg) translateY(0px);
            transform: rotate(1deg) translateY(0px); }
  50% {
    -webkit-transform: rotate(1deg) translateY(-1000px);
            transform: rotate(1deg) translateY(-1000px); }
  100% {
    opacity: 0.97;
    -webkit-transform: rotate(1deg) translateY(-2000px);
            transform: rotate(1deg) translateY(-2000px); } }

@keyframes column-up {
  0% {
    -webkit-transform: rotate(1deg) translateY(0px);
            transform: rotate(1deg) translateY(0px); }
  50% {
    -webkit-transform: rotate(1deg) translateY(-1000px);
            transform: rotate(1deg) translateY(-1000px); }
  100% {
    opacity: 0.97;
    -webkit-transform: rotate(1deg) translateY(-2000px);
            transform: rotate(1deg) translateY(-2000px); } }
@-webkit-keyframes column-down {
  0% {
    -webkit-transform: rotate(1deg) translateY(-2000px);
            transform: rotate(1deg) translateY(-2000px); }
  50% {
    -webkit-transform: rotate(1deg) translateY(-1000px);
            transform: rotate(1deg) translateY(-1000px); }
  100% {
    opacity: 0.95;
    -webkit-transform: rotate(1deg) translateY(0px);
            transform: rotate(1deg) translateY(0px); } }
@keyframes column-down {
  0% {
    -webkit-transform: rotate(1deg) translateY(-2000px);
            transform: rotate(1deg) translateY(-2000px); }
  50% {
    -webkit-transform: rotate(1deg) translateY(-1000px);
            transform: rotate(1deg) translateY(-1000px); }
  100% {
    opacity: 0.95;
    -webkit-transform: rotate(1deg) translateY(0px);
            transform: rotate(1deg) translateY(0px); } }
